import React from 'react';

export default function Card({max,min,name,img,onClose}) {
  // acá va tu código
  return(
  <div>
    <button onClick={onClose}>X</button>
    <h2>Nombre {name}</h2>
    <div>
      <h6>Min</h6>
      <p>{min}</p>
    </div>
    <div>
      <h6>Max</h6>
      <p>{max}</p>
    </div>
    <img src={`http://openweathermap.org/img/wn/${img}@2x.png`} alt="imagen tempuratura" />
  </div>
  )
};