import React from 'react';
import Card from './Card';

export default function Cards(props) {
  // acá va tu código
  // tip, podés usar un map
  return (
    <div>
      {
        props.citys.map(elementos => (
          <Card
            max={elementos.main.temp_max}
            min={elementos.main.temp_min}
            name={elementos.name}
            img={elementos.weather[0].icon}
            onClose={() => alert(elementos.name)}
          />
      ))
    }
    </div>
  )
};